body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Chilanka", cursive;
  transition: ease all 1s;
}

.Global_Bg {
  width: 100%;
  height: 100%;
  background: #833ab4;
  background: linear-gradient(27deg, #833ab4 9%, #1d9afd 66%, #96feff 100%);
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

body {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  overflow: auto;
}

.mainbox {
  width: 900px;
  display: flex;
  box-sizing: border-box;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 45px rgba(0, 0, 0, 0.5);
}

.lists_left_side {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1rem;
  width: 230px;
  border-radius: 20px 00px 0px 20px;
  background: #560979;
  background: linear-gradient(34deg, #560979 0%, #00f1ff 100%);
}

.lists_left_side__PlusLists {
  padding-top: 10px;
  font-size: 47px;
  color: white;
}

.lists_left_side__PlusLists:hover {
  cursor: pointer;
}

.lists_left_side__AllMainList {
  list-style: none;
  margin: 0;
  color: white;
  font-size: 26px;
  padding: 0.5rem 4rem;
  padding-bottom: 2rem;
  font-weight: 600;
  letter-spacing: 1.5px;
}

.lists_left_side__OneMainList::-webkit-scrollbar {
  width: 1em;
}

.lists_left_side__OneMainList::-webkit-scrollbar-thumb {
  background-color: rgba(180, 64, 247, 0.616);
  outline: 1px solid slategrey;
  border-radius: 20px;
}

.lists_left_side__OneMainList button {
  display: none;
}

.lists_left_side__OneMainList {
  padding: 0.3rem;
  display: flex;
  overflow: auto;
  justify-content: space-between;
  width: 170px;
  max-width: 200px;
}

.lists_left_side__OneMainList:hover {
  text-decoration: underline;
  -webkit-text-decoration-color: rgba(207, 206, 206, 0.651);
          text-decoration-color: rgba(207, 206, 206, 0.651);
}

.lists_left_side__OneMainList:hover button {
  cursor: pointer;
  display: inline-block;
  background: none;
  font-size: 20px;
  border: none;
  outline: none;
}

.lists_left_side__Click_here {
  color: white;
  font-size: 22px;
  letter-spacing: 1.5px;
  font-weight: 1, 400;
}

.wrapper::-webkit-scrollbar {
  width: 1em;
}

.wrapper::-webkit-scrollbar-thumb {
  background-color: rgba(185, 64, 255, 0.466);
  outline: 1px solid slategrey;
  border-radius: 20px;
}

.wrapper {
  font-family: "Chilanka", cursive;
  padding: 20px;
  width: 100%;
  border-radius: 0px 20px 20px 0px;
  background: white;
  background: linear-gradient(53deg, white 49%, #2de0fd 76%, #78a1ff 100%);
  max-height: 600px;
  overflow: auto;
}

.wrapper__title {
  font-size: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper__title--font {
  letter-spacing: 2px;
  font-family: "Chilanka", cursive;
}

.wrapper__form {
  margin: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper__input {
  padding: 5px;
  border: 2px solid rgba(174, 43, 250, 0.329);
  border-radius: 4px;
  font-size: 18px;
  width: 60%;
  outline: none;
}

.wrapper__input:focus {
  border: 3px solid rgba(174, 43, 250, 0.329);
}

.wrapper__button {
  padding: 5px;
  border-radius: 4px;
  font-size: 18px;
  font-weight: 600;
  outline: none;
  background: white;
  border: 2px solid rgba(174, 43, 250, 0.329);
}

.wrapper__button:hover {
  border: 3px solid rgba(174, 43, 250, 0.719);
}

.red {
  color: red;
}

.noPad {
  margin: 1rem auto 0 auto;
}

.done {
  text-decoration: line-through;
  color: gray;
  background: white !important;
  background: linear-gradient(254deg, white 0%, #f0ffb5 62%, rgba(129, 255, 0, 0.75) 100%) !important;
}

.ToDoItem {
  background: white;
  font-family: "Chilanka", cursive;
  font-size: 19px;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
}

.ToDoItem__edit_title {
  color: #33fa01;
}

.ToDoItem button {
  display: none !important;
  background: none;
  border: none;
  outline: none;
  font-size: 18px;
}

.ToDoItem:hover button {
  display: inline-block !important;
  cursor: pointer;
}

.SurpriseYourself {
  margin: 3rem auto;
  font-size: 26px;
  font-family: "Lato", sans-serif;
}

.imput_editor {
  font-size: 18px;
}
